import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import List from "./List";
import Map from "./Map";

export default class Menu extends Component {
  constructor (props) {
    super(props);
    this.state = {
      visible: props.visible,
      className: 'menu__background'
    };

  }

  click = e => {

    e.target.parentNode.parentNode.parentNode.classList.remove('visible');
    this.props.changeParentState();
  }

  shouldComponentUpdate(nextProps, nextState) {

    if(nextProps.visible !== this.state.visible) {
      this.setState({visible: nextProps.visible});
      if(this.state.visible) {
        this.setState({className: 'menu__background visible'});
      }
      else {
        this.setState({className: 'menu__background'});
      }


      return true;
    }
    else return false;
  }



  render() {

    return (
      <Router>
      <div className={this.state.className} onClick={this.click}>
        <div className='nav paper'>
            <ul className='nav__list'>
              <li onClick={this.click}>
                <Link className="nav__link" to="/">Список</Link>
              </li>
              <li onClick={this.click}>
                <Link className="nav__link" to="/map/">Карта</Link>
              </li>
            </ul>
          </div>
        </div>
        <div className="content">
          <Switch>
            <Route path="/" exact component={List} />
            <Route path="/map" component={Map} />
          </Switch>
        </div>

      </Router>
    );
  }

}
