import React, { Component } from 'react';
import Menu from './components/Menu';
import Header from './components/Header';
import Footer from './components/Footer';
import './App.css';

class App extends Component {
  constructor() {
    super();
    this.state = {
      isMenuVisible: true
    }
  }

  stateToggle = () => {
    this.setState({isMenuVisible: !this.state.isMenuVisible});
  }



  render() {
    return (
      <div className="page">
      <Header onChange={this.stateToggle}/>
      <Menu visible={this.state.isMenuVisible} changeParentState={this.stateToggle}/>
      <Footer />
      </div>
    );
  }
}

export default App;
