import React, { Component } from 'react';

export default class Card extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (

        <div className="card paper">
          <img src={this.props.src} width='200' alt="" className='image'/>
          <div className="big">{this.props.title || 'Загрузка...'}</div>
          <div className="small">{this.props.date}</div>
          <div className="medium">{this.props.description}</div>
        </div>
    )
  }
}
